import React, { useEffect, useRef } from "react";
import { refreshWidgetData } from "./services/api/refreshService"; 
import useToast from "./hooks/useToast"; 
import useDroppableArea from "./hooks/useDroppableArea"; 
import { MSG_REFRESH_ERROR, MSG_REFRESH_SUCCESS } from "./utils/toastMessages";
import { useSelector } from "react-redux";

const WidgetLifecycle = ({ children }) => {
  const { handleDrop } = useDroppableArea();
  const { showSuccessToast, showErrorToast } = useToast();
  const initialDraggedData = useSelector(
    (state) => state.droppedObject.droppedObjectData.initialDraggedData
  );

  console.log("initialDraggedData in widget-lifecycle", initialDraggedData);

  const hasOnRefreshRun = useRef(false); // Prevent duplicate onRefresh registrations
  console.log("---[widget lifecycle]--- starts");

  useEffect(() => {
    if (!window.widget) return;

    // Prevent multiple event registrations
    if (hasOnRefreshRun.current) {
      console.warn("[WidgetLifecycle] ⏳ onRefresh is already registered. Ignoring duplicate.");
      return;
    }
    hasOnRefreshRun.current = true; // Mark as registered
    const onRefresh = async () => {
      console.log("[WidgetLifecycle] 🔄 onRefresh triggered!");
    
      if (
        !initialDraggedData || 
        !initialDraggedData.data || 
        !initialDraggedData.data.items || 
        initialDraggedData.data.items.length === 0
      ) {
        console.error("[WidgetLifecycle] ⚠️ initialDraggedData is missing or invalid:", initialDraggedData);
        return; // 🚨 Prevents execution if the data is missing
      }
    
      const objectId = initialDraggedData.data.items[0]?.objectId;
      const objectType = initialDraggedData.data.items[0]?.objectType;
    
      if (!objectId || !objectType) {
        console.error("[WidgetLifecycle] ❌ Missing objectId or objectType. Skipping refresh.");
        return;
      }
    
      try {
        const dataItems = initialDraggedData.data.items;
        await refreshWidgetData(dataItems, handleDrop);
        showSuccessToast(MSG_REFRESH_SUCCESS);
      } catch (error) {
        console.error("[WidgetLifecycle] ❌ Error during refresh:", error);
        showErrorToast(MSG_REFRESH_ERROR);
      }
    };
    

    window.widget.addEvent("onRefresh", onRefresh);
    console.log("[WidgetLifecycle] ✅ onRefresh event registered successfully");

// ❌ DO NOT REMOVE onRefresh on unmount to prevent re-adding it unnecessarily
}, []); // 👈 Empty dependency array ensures it runs **only once**

return null;
};


export default WidgetLifecycle;
